import React from 'react'
import { graphql, Link } from "gatsby"

function Navlink({ data: { allMarkdownRemark } }) {
    return (
        <div className='mt-6 grid grid-cols-3 sm:grid-cols-3 lg:grid-cols-6 xl:gap-x-10'>
            {allMarkdownRemark.nodes.map(({ id, frontmatter }) => {
                return <div key={id} className='card w-60 bg-base-100 shadow-xl m-5'>
                    <figure>
                        <img className='rounded-xl w-full aspect-square' src={frontmatter.cover} />
                    </figure>
                    <div className='card-body'>
                        <p className='card-title'>{frontmatter.title}</p>
                        <Link className='underline' to={frontmatter.path}>อ่านต่อ</Link>
                    </div>
                </div>
            })}
        </div>
    )
}

export default Navlink

export const query = graphql`
    query {
        allMarkdownRemark {
            nodes {
                id
                frontmatter {
                    title
                    path
                    cover
                }
            }
        }
    }
`